import { useEffect } from 'react';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import { getChangeAccount } from '../../api/setting';
import { getUserData } from '../../api/userprofile';
import Input from '../Input/Input';
import styled from './SettingInput.module.scss';

function SettingInput() {
	const [userOriginData, setUserOriginData] = useState(null);
	console.log(userOriginData);
	const [accountLength, setAccountLength] = useState(0);
	const [account, setAccount] = useState('');
	const [name, setUsername] = useState('');
	const [nameLength, setNameLength] = useState(0);
	const [email, setEmail] = useState('');
	const [password, setPassword] = useState('');
	const [checkPassword, setCheckPassword] = useState('');

	const authToken = localStorage.getItem('authToken');
	const userId = localStorage.getItem('userId');
	const navigate = useNavigate();

	const handleAccountChange = (e) => {
		e.preventDefault();
		const inputValue = e.target.value;
		// 
		const inputWithoutSpaces = inputValue.replace(/\s+/g, '');
		const inputLength = inputWithoutSpaces.length;
		setAccount(inputValue);
		setAccountLength(inputLength);
	};

	const handleUserNameChange = (e) => {
		e.preventDefault();
		const inputValue = e.target.value;
		//  trim  string 
		const inputWithoutSpaces = inputValue.replace(/\s+/g, '');
		const inputLength = inputWithoutSpaces.length;
		setNameLength(inputLength);
		setUsername(inputWithoutSpaces);
	};

	const handleEmailChange = (e) => {
		e.preventDefault();
		const inputValue = e.target.value;
		//  trim  string 
		const inputWithoutSpaces = inputValue.replace(/\s+/g, '');
		setEmail(inputWithoutSpaces);
	};

	const handlePasswordChange = (e) => {
		e.preventDefault();
		const inputValue = e.target.value;
		//  trim  string 
		const inputWithoutSpaces = inputValue.trim();
		setPassword(inputWithoutSpaces);
	};

	const handleCheckPasswordChange = (e) => {
		e.preventDefault();
		const inputValue = e.target.value;
		//  trim  string 
		const inputWithoutSpaces = inputValue.trim();
		setCheckPassword(inputWithoutSpaces);
	};

	// save button event
	const handleSaveClick = async (e) => {
		e.preventDefault();
		if (account.length === 0) {
			alert('');
			return;
		} else if (accountLength > 50) {
			alert('!');
			return;
		} else if (nameLength > 50) {
			alert('!');
			return;
		} else if (name.length === 0) {
			alert('');
			return;
		} else if (email.length === 0) {
			alert('Email ');
			return;
		} else if (password.length === 0) {
			alert('');
			return;
		} else if (checkPassword.length === 0) {
			alert('');
			return;
		} else if (password !== checkPassword) {
			alert('');
			return;
		}

		const result = await getChangeAccount(
			userId,
			authToken,
			account,
			email,
			name,
			password,
			checkPassword,
		);

		if (result) {
			// 
			Swal.fire({
				position: 'top',
				title: '',
				timer: 1000,
				icon: 'success',
				showConfirmButton: false,
			});
			setPassword('');
			setCheckPassword('');

			// 
			const data = await getUserData(userId, authToken);
			setUserOriginData(data);
			setAccount(data.account);
			setUsername(data.name);
			setEmail(data.email);
			return;
		}

		// 
		Swal.fire({
			position: 'top',
			title: '',
			timer: 1000,
			icon: 'error',
			showConfirmButton: false,
		});
	};

	// 
	useEffect(() => {
		const fetchUserData = async () => {
			try {
				if (!authToken) {
					navigate('/login');
					return;
				}
				// 
				const data = await getUserData(userId, authToken);
				setUserOriginData(data);
				setAccount(data.account);
				setUsername(data.name);
				setEmail(data.email);
			} catch (error) {
				console.error(error);
			}
		};
		fetchUserData();
	}, []);

	return (
		<div className={styled.inputWrap}>
			<div className={styled.accountInput}>
				<Input
					inputTitle=''
					type='account'
					value={account}
					placeholder=''
					onChange={handleAccountChange}
				/>

				<div className={styled.countWrap}>
					{/*  */}
					<div>
						<span className={styled.countTitle}>{accountLength > 50 ? '' : ''}</span>
					</div>

					<div>
						<span className={styled.countNumber}>
							{accountLength <= 0 ? '' : `: ${accountLength} / 50`}
						</span>
					</div>
				</div>
			</div>

			<div className={styled.accountInput}>
				<Input
					inputTitle=''
					type='name'
					value={name}
					placeholder=''
					onChange={handleUserNameChange}
				/>

				<div className={styled.countWrap}>
					{/*  */}
					<div>
						<span className={styled.countTitle}>{nameLength > 50 ? '' : ''}</span>
					</div>

					<div>
						<span className={styled.countNumber}>
							{nameLength <= 0 ? '' : `: ${nameLength} / 50`}
						</span>
					</div>
				</div>
			</div>

			<Input
				inputTitle='Email'
				type='email'
				value={email}
				placeholder=' Email'
				onChange={handleEmailChange}
			/>
			<Input
				inputTitle=''
				type='password'
				value={password}
				placeholder=''
				onChange={handlePasswordChange}
			/>
			<Input
				inputTitle=''
				type='password'
				value={checkPassword}
				placeholder=''
				onChange={handleCheckPasswordChange}
			/>
			{/*  */}
			<div className={styled.buttonWrap}>
				<button className={styled.saveButton} onClick={handleSaveClick}>
					
				</button>
			</div>
		</div>
	);
}

export default SettingInput;
