import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import { register } from '../../api/auth';
import { useAuthSignUp } from '../../contexts/AuthSignUpContext';
import Input from '../Input/Input';
import MainButton from '../MainButton/MainButton';
import styled from './RegisterInput.module.scss';

function RegisterInput() {
	// useContext
	const {
		accountLength,
		account,
		name,
		email,
		password,
		checkPassword,
		handleAccountChange,
		handleUserNameChange,
		handleEmailChange,
		handlePasswordChange,
		handleCheckPasswordChange,
	} = useAuthSignUp();

	const navigate = useNavigate();

	//  event
	const handleSignUpSubmit = async (e) => {
		e.preventDefault();
		if (account.length === 0) {
			alert('');
			return;
		} else if (accountLength > 50) {
			alert('!');
			return;
		} else if (name.length === 0) {
			alert('');
			return;
		} else if (email.length === 0) {
			alert('Email ');
			return;
		} else if (password.length === 0) {
			alert('');
			return;
		} else if (checkPassword.length === 0) {
			alert('');
			return;
		} else if (password !== checkPassword) {
			alert('');
			return;
		}

		const { registered, success } = await register({
			account,
			name,
			email,
			password,
			checkPassword,
		});

		if (success) {
			Swal.fire({
				position: 'top',
				title: '',
				timer: 1500,
				icon: 'success',
				showConfirmButton: false,
			});

			// 
			navigate('/login');

			return;
		}

		if (registered === 'Error: account already exists!') {
			alert('');
		}

		if (registered === 'Error: email already exists!') {
			alert('Email ');
		}

		// 
		Swal.fire({
			position: 'top',
			title: '',
			timer: 1500,
			icon: 'error',
			showConfirmButton: false,
		});
	};

	return (
		<div className={styled.inputCon}>
			<div className={styled.inputWrap}>
				<Input
					inputTitle=''
					type='account'
					value={account}
					placeholder=''
					onChange={handleAccountChange}
				/>
				<div className={styled.countWrap}>
					{/*  */}
					<span className={styled.countTitle}>{accountLength > 50 ? '' : ''}</span>
					<span className={styled.countNumber}>
						{accountLength <= 0 ? '' : `: ${accountLength} / 50`}
					</span>
				</div>
			</div>

			<div className={styled.inputWrap}>
				{/*  */}
				<Input
					inputTitle=''
					type='username'
					value={name}
					placeholder=''
					onChange={handleUserNameChange}
				/>
			</div>

			{/*  */}
			<div className={styled.inputWrap}>
				{/* email */}
				<Input
					inputTitle='Email'
					type='email'
					value={email}
					placeholder=' Email'
					onChange={handleEmailChange}
				/>
			</div>

			<div className={styled.inputWrap}>
				{/* email */}
				<Input
					inputTitle=''
					type='password'
					value={password}
					placeholder=''
					onChange={handlePasswordChange}
				/>
			</div>

			<div className={styled.inputWrap}>
				<Input
					inputTitle=''
					type='password'
					value={checkPassword}
					placeholder=''
					onChange={handleCheckPasswordChange}
				/>
			</div>

			<MainButton buttonTitle='' onClick={handleSignUpSubmit} />
		</div>
	);
}

export default RegisterInput;
