import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import { adminLogin } from '../../api/auth';
import Input from '../Input/Input';
import MainButton from '../MainButton/MainButton';
import styled from './AdminInput.module.scss';

function AdminInput() {
	const [account, setAccount] = useState('');
	const [password, setPassword] = useState('');

	const navigate = useNavigate();

	const handleAccountChange = (e) => {
		const inputValue = e.target.value;
		// 
		const inputWithoutSpaces = inputValue.trim();
		setAccount(inputWithoutSpaces);
	};

	const handlePasswordChange = (e) => {
		const inputValue = e.target.value;
		//  trim  string 
		const inputWithoutSpaces = inputValue.trim();
		setPassword(inputWithoutSpaces);
	};

	//  event
	const handleLoginSubmit = async (e) => {
		e.preventDefault();
		if (account.length === 0) {
			alert('');
		}
		if (password.length === 0) {
			alert('');
		}

		const { success, authToken } = await adminLogin({
			account,
			password,
		});

		if (success) {
			localStorage.setItem('adminAuthToken', authToken);

			// 
			Swal.fire({
				position: 'top',
				title: '',
				timer: 1000,
				icon: 'success',
				showConfirmButton: false,
			});
			navigate('/admin/main');
			return;
		}

		// 
		Swal.fire({
			position: 'top',
			title: '',
			timer: 1000,
			icon: 'error',
			showConfirmButton: false,
		});
	};

	return (
		<div className={styled.inputCon}>
			<div className={styled.inputWrap}>
				<Input
					inputTitle=''
					type='account'
					value={account}
					placeholder=''
					onChange={handleAccountChange}
				/>
			</div>

			{/*  */}
			<div className={styled.inputWrap}>
				<Input
					inputTitle=''
					type='password'
					value={password}
					placeholder=''
					onChange={handlePasswordChange}
				/>
			</div>

			<MainButton buttonTitle='' onClick={handleLoginSubmit} />
		</div>
	);
}

export default AdminInput;
